import React, { Component } from 'react';
import { Link } from 'react-router-dom';
class Bookerp extends Component {
    render(){
        return(
            <main className="container">
                <article>

                    <header className="subtop subtop--bg1">
                        <div className="inner">
                            <div className="subtop__content">
                                <h2 className="subtop__title">TECHNOLOGY</h2>
                                <p className="subtop__text">
                                    고객의 꿈을 함께 꾸고 만드는 맞춤형 웹서비스 전문 CHSOFT입니다.
                                </p>
                            </div>
                        </div>
                    </header>

                    <div className="contents">
                        <div className="inner">
                            <div className="technology">
                                <p className="technology__text">
                                    고객님과 함께 꿈을 만듭니다.
                                </p>
                                <h3 className="technology__title">
                                    출판ERP
                                </h3>

                                <div className="tab">
                                    <ul className="tab__list">
                                        <li className="tab__item">
                                            <Link to="/homepage" className="tab__link">
                                                <span>홈페이지</span>
                                            </Link>
                                        </li>
                                        <li className="tab__item">
                                            <Link to="/erp" className="tab__link"> 
                                                <span>ERP</span>
                                            </Link>
                                        </li>
                                        <li className="tab__item">
                                            <Link to="/bookerp" className="tab__link active">
                                                <span>출판ERP</span>
                                            </Link>
                                        </li>
                                    </ul>
                                </div>

                                <div className="technology__info">
                                    <div className="technology__info__image">
                                        <p>
                                            <img src={require("../../img/tech/tech_img_03.png")} alt=""/>
                                        </p>
                                    </div>
                                    <div className="technology__info__content">
                                        <p className="technology__info__text1">
                                            20여년의 경험과 10년이상의 개발 노하우로 출판 ERP 제작!
                                        </p>
                                        <h4 className="technology__info__title">
                                            고객관리, 발주, 매출관리, 재고관리를 한번에!
                                        </h4>
                                        <p className="technology__info__text2">
                                            여러 출판사와 함께한 노하우로 출판사에 필요한 모든 기능을 제공해드립니다.<br/>
                                            구축기간의 최소화, 비용의 최소화, 정보관리의 최대화로 ERP를 운영하세요.
                                        </p>
                                        <div className="technology__info__btn-box">
                                            <Link to="/contactus" class="js-btn">
                                                <span>제작, 견적문의</span>
                                            </Link>
                                        </div>
                                    </div>
                                </div>

                                <div className="technology__portfolio">
                                    <div className="technology__portfolio__item">
                                        <div className="technology__portfolio__image">
                                            <p>
                                                <img src={require("../../img/tech/tech_port_img_06.png")} alt=""/>
                                            </p>
                                        </div>
                                        <div className="technology__portfolio__btn-box">
                                            <a href="http://bookerp3.testsite.co.kr/login.php?mode=demo" target="_blank" className="pot_btn cy" rel="noopener noreferrer">ERP 체험</a>
                                        </div>
                                    </div>
                                    <div className="technology__portfolio__item">
                                        <div className="technology__portfolio__image">
                                            <p>
                                                <img src={require("../../img/tech/tech_port_img_07.png")} alt=""/>
                                            </p>
                                        </div>
                                        <div className="technology__portfolio__btn-box">
                                            <a href="http://crm.testsite.co.kr/open_content/login/login.php?mode=demo" target="_blank" className="pot_btn cy" rel="noopener noreferrer">CRM 체험</a>
                                        </div>
                                    </div>                                    
                                </div>

                            </div>
                        </div>
                    </div>

                </article>
            </main>
        )
    }
}

export default Bookerp;