import React, { Component } from 'react';
import SubPortfolio from '../../components/sub_portfolio/sub_portfolio';
import '../../css/portfolio.scss';

class portfolio extends Component {
    render(){
        return(
            <main className="container">
                <article>

                    <header className="subtop subtop--bg1">
                        <div className="inner">
                            <div className="subtop__content">
                                <h2 className="subtop__title">PORTFOLIO</h2>
                                <p className="subtop__text">
                                    고객의 꿈을 함께 꾸고 만드는 맞춤형 웹서비스 전문 CHSOFT입니다.
                                </p>
                            </div>
                        </div>
                    </header>

                    <div className="contents">
                        <div className="inner">

                            <div className="portfolio">
                                <p className="portfolio__text">
                                    섬세한 고객만족전략과 검증된 웹기술이 귀사를 돋보이게 할 것입니다.
                                </p>
                                <h3 className="portfolio__title">
                                    PORTFOLIO
                                </h3>
                                <SubPortfolio/>
                            </div>

                        </div>
                    </div>

                </article>
            </main>
        )
    }
}

export default portfolio;