
import React, { Component } from 'react';
import { Link } from 'react-router-dom';


class SubPortfolio extends Component {

    render(){

        const portfolio_items = [
            {
                id: 1,
                image : '/imgs/main/section3/1.jpg',
                link : '/',
                type : 'WEBDESIGN',
                name : '대전기후환경네트워크'
            },
            {
                id: 2,
                image : '/imgs/main/section3/2.jpg',
                link : '/',
                type : 'WEBDESIGN',
                name : '교량진로적성개발원'
            },
            {
                id: 3,
                image : '/imgs/main/section3/3.jpg',
                link : '/',
                type : 'WEBDESIGN',
                name : '(주)삼원자판기'
            },
            {
                id: 4,
                image : '/imgs/main/section3/4.jpg',
                link : '/',
                type : 'WEBDESIGN',
                name : '경상북도청소년성문화센터'
            },
            {
                id: 5,
                image : '/imgs/main/section3/5.jpg',
                link : '/',
                type : 'WEBDESIGN',
                name : '문화시민운동사무국'
            },
            {
                id: 6,
                image : '/imgs/main/section3/6.jpg',
                link : '/',
                type : 'WEBDESIGN',
                name : '(주)씨디씨뉴매틱'
            },
            {
                id: 7,
                image : '/imgs/main/section3/7.jpg',
                link : '/',
                type : 'WEBDESIGN',
                name : '빙도리'
            },
            {
                id: 8,
                image : '/imgs/main/section3/8.jpg',
                link : '/',
                type : 'WEBDESIGN',
                name : "대구광역시 서구도시재생지원센터"
            },
            {
                id: 9,
                image : '/imgs/main/section3/9.jpg',
                link : '/',
                type : 'WEBDESIGN',
                name : '다품문화예술협회'
            },
        ];
        
        const portfolio_item = portfolio_items.map((el)=>
            <div className="portfolio__item" key={el.id}>
                <Link to={el.link} className="portfolio__link">
                    <div className="portfolio__inner">
                        <div className="portfolio__item__image" style={{ 
                            backgroundImage: `url(${el.image})`
                        }}></div>
                        <div className="portfolio__bg">
                            <div className="portfolio__bg__content">
                                <p className="portfolio__item__type">
                                    {el.type}
                                </p>
                                <p className="portfolio__item__title">
                                    {el.name}
                                </p>
                                <p className="portfolio__item__btn-box">
                                    <span class="portfolio__item__link">
                                        자세히 보기
                                    </span>
                                </p>
                            </div>
                        </div>
                    </div>
                </Link>
            </div>
        )
        
        
        return(
            <>
                <div className="portfolio__items">
                    {portfolio_item}
                </div>

                <div className="pagenav">
                    <a href="#none" className="page prev"><span className="blind">이전</span></a>
                    <a href="#none" className="num on">1</a> 
                    <a href="#none" className="num">2</a>
                    <a href="#none" className="num">3</a>
                    <a href="#none" className="num">4</a>
                    <a href="#none" className="num">5</a>
                    <a href="#none" className="page next"><span className="blind">다음</span></a>
                </div>
            </>
        )
    }
}

export default SubPortfolio;