import React, { Component } from 'react';
import '../../css/info.scss';

class aboutus extends Component {
    render(){
        return(
            <main className="container">
                <article>

                    <header className="subtop subtop--bg1">
                        <div className="inner">
                            <div className="subtop__content">
                                <h2 className="subtop__title">ABOUT US</h2>
                                <p className="subtop__text">
                                    고객의 꿈을 함께 꾸고 만드는 맞춤형 웹서비스 전문 CHSOFT입니다.
                                </p>
                            </div>
                        </div>
                    </header>

                    <div className="contents contents--pdb0">
                        <div className="aboutus">
                            <div className="inner">
                                <p className="aboutus__text">
                                    20여년의 경험과 10년이상의 개발 노하우의 차별화된 서비스로
                                </p>
                                <p className="aboutus__text aboutus__text--large">
                                    고객의 꿈을 함께 꾸며 만들어 갈 것입니다. 
                                </p>
                                <div className="aboutus__items">
                                    <div className="aboutus__item aboutus__item--left-line">
                                        <p className="aboutus__item__image">
                                            <img src={require("../../img/info/about_img_01.png")} alt=""/>
                                        </p>
                                        <dl>
                                            <dt>
                                                <img src={require("../../img/info/about_txt1.png")} alt=""/>
                                            </dt>
                                            <dd>
                                                UI/IA 분석에 따른 전략적 디자인 설계<br/>
                                                고객요구, 시장분석에 의한 웹디자인 전략 마련<br/>
                                                Flash, Ajax, DHTML, JAVA 등을<br/>
                                                활용한 멀티미디어 웹디자인<br/>
                                                최근 웹디자인 트렌드에 따른 고품격 디자인 제시<br/>
                                                국내최초 멀티미디어 감성적<br/>
                                                개인관리 사이트 디자인 노하우
                                            </dd>
                                        </dl>
                                    </div>
                                    <div className="aboutus__item aboutus__item--left-line">
                                        <p className="aboutus__item__image">
                                            <img src={require("../../img/info/about_img_02.png")} alt=""/>
                                        </p>
                                        <dl>
                                            <dt>
                                                <img src={require("../../img/info/about_txt2.png")} alt=""/>
                                            </dt>
                                            <dd>
                                                SI사업의 관리포털 개발 기술<br/>
                                                (국방부 나라사랑카드 등)<br/>
                                                웹접근성 및 웹UI 개발 기술<br/>
                                                윈도우, 리눅스, 유닉스, 오라클 등<br/>
                                                각종 플랫폼 기반 개발 및 최적화 기술<br/>
                                                스마트카드, RFID, 바코드 연동 시스템 개발 기술<br/>
                                                모바일 네트워킹 프로그램 기술
                                            </dd>
                                        </dl>
                                    </div>
                                    <div className="aboutus__item">
                                        <p className="aboutus__item__image">
                                            <img src={require("../../img/info/about_img_03.png")} alt=""/>
                                        </p>
                                        <dl>
                                            <dt>
                                                <img src={require("../../img/info/about_txt3.png")} alt=""/>
                                            </dt>
                                            <dd>
                                                지식관리 개념을 선도적으로 적용한<br/>
                                                국내 최대 유머사이트 개발운영<br/>
                                                이미지, 음악, 영상을 이용한<br/>
                                                최초의 멀티미디어 메일 개발 운영<br/>
                                                애견포털, 이미지포털, 문화포털, 인테리어포털 등<br/>
                                                다양한 포털 서비스 수주<br/>
                                                메시징, 커뮤니티, 검색 등 웹서비스 관련<br/>
                                                다양한 솔루션 개발 기술 보유
                                            </dd>
                                        </dl>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="company-info">
                            <div className="inner">
                                <h2 className="company-info__logo">
                                    <img src={require("../../img/info/about_logo.png")} alt="chsoft"/>
                                </h2>
                                <div className="company-info__content">
                                    <ul>
                                        <li>
                                            <strong>회사명</strong>
                                            <span>(주) CHSOFT</span>
                                        </li>
                                        <li>
                                            <strong>설립일</strong>
                                            <span>2016년 03월 09일</span>
                                        </li>
                                        <li>
                                            <strong>사업분야</strong>
                                            <span>고급 웹사이트 개발 / ERP 구축 / 웹 컨설팅 / 웹사이트 유지관리 및 운영대행</span>
                                        </li>
                                        <li>
                                            <strong>임직원</strong>
                                            <span>15명 (10년 이상 경력자 8명)</span>
                                        </li>
                                        <li>
                                            <strong>주소</strong>
                                            <span>대구광역시 서구 와룡로 307 8층 804호</span>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>

                </article>
            </main>
        )
    }
}

export default aboutus;