import React, { Component } from 'react';

class SubBbs extends Component {

    render(){
        function activeMethod(event) {
            const current = event.currentTarget;
            const chkActive = current.classList.value.indexOf("active");
            console.log(current);
            closeMothodAll();
        
            if (chkActive === -1) {
                current.classList.add("active");
            }
        }

        function closeMothodAll() {
            let bx = document.getElementsByClassName("bbs-list");
        
            for (let i = 0; i < bx[0].children.length; i++) {
                bx[0].children[i].classList.remove("active");
        
            // console.log(bx[0].children.length);
            }
        }
        const bbs_items = [
            {
                id: 1,
                subject : '2018년 10월 23일 위탁등록업체 선정',
                text : '중소기업진흥공단 청년벤처사관학교 위탁등록업체 선정<br/>위탁등록번호 : 2912',
                date : '2019-01-15'
            },
            {
                id: 2,
                subject : '2018년 09월07일 벤처기업 인증',
                text : '기술보증기금',
                date : '2018-09-07'
            },
            {
                id: 3,
                subject : '2017년 11월8일 저작권등록(5건)',
                text : '숙박통합관리시스템<br>축산구매관리시스템<br>축산가공관리시스템<br>공장생산관리시스템<br>출판통합관리시스템',
                date : '2017-11-20'
            },
            {
                id: 4,
                subject : '대마도 자전거 라이딩 대회',
                text : '일시 : 2014년 10월03일 ~ 04일<br>장소 : 대마도종주<br>준비물 : 여권, MTB(사이클) <br><br>가을을 맞이하여 CH직원 자전거 라이딩대회를 개최합니다.',
                date : '2016-10-31'
            },
            {
                id: 5,
                subject : 'CH 홈페이지가 새로이 오픈하였습니다',
                text : '홈페이지를 오픈하였습니다.<br>언제든지 문의주시면 성심으로 답변드리겠습니다.<br>찾아뵙는 서비스는 기본입니다.',
                date : '2016-03-09'
            },
        ];

        const bbs_item = bbs_items.map((el)=>
            <div className="bbs-list__item" key={el.id} onClick={activeMethod}>
                <h3 className="bbs-list__item__title">
                    {el.subject}
                </h3>
                <div className="bbs-list__item__content">
                    <p dangerouslySetInnerHTML={{__html : el.text}}>
                    </p>
                </div>
                <p className="bbs-list__item__date">
                    {el.date}
                </p>
                <span className="bbs-list__item__btn">
                    <span className="blind">토글버튼</span>
                </span>
            </div>
        )

        return(
            
                <div className="bbs-list">
                    {bbs_item}
                </div>
            
        )
    }
}

export default SubBbs;
