
import React, { Component } from 'react';
import { Link } from 'react-router-dom';


class MainPortfolio extends Component {

    render(){

        const portfollo_items = [
            {
                id: 1,
                image : '/imgs/main/section3/1.jpg',
                link : '/',
                name : '대전기후환경네트워크'
            },
            {
                id: 2,
                image : '/imgs/main/section3/2.jpg',
                link : '/',
                name : '교량진로적성개발원'
            },
            {
                id: 3,
                image : '/imgs/main/section3/3.jpg',
                link : '/',
                name : '(주)삼원자판기'
            },
            {
                id: 4,
                image : '/imgs/main/section3/4.jpg',
                link : '/',
                name : '경상북도청소년성문화센터'
            },
            {
                id: 5,
                image : '/imgs/main/section3/5.jpg',
                link : '/',
                name : '문화시민운동사무국'
            },
            {
                id: 6,
                image : '/imgs/main/section3/6.jpg',
                link : '/',
                name : '(주)씨디씨뉴매틱'
            },
            {
                id: 7,
                image : '/imgs/main/section3/7.jpg',
                link : '/',
                name : '빙도리'
            },
            {
                id: 8,
                image : '/imgs/main/section3/8.jpg',
                link : '/',
                name : "대구광역시 서구도시재생지원센터"
            },
            {
                id: 9,
                image : '/imgs/main/section3/9.jpg',
                link : '/',
                name : '다품문화예술협회'
            },
        ];
        
        const portfolio_item = portfollo_items.map((el)=>
            <div className="portfollo__item" key={el.id}>
                <Link to={el.link} className="portfollo__link">
                    <div className="portfollo__image" style={{ 
                        backgroundImage: `url(${el.image})`
                    }}></div>
                    <div className="portfollo__bg">
                        <div className="portfollo__bg__content">
                            <span>
                                {el.name}
                            </span>
                        </div>
                    </div>
                </Link>
            </div>
        )
        
        
        return(
            <div className="portfollo">
                <div className="portfollo__content">
                    <div className="portfollo__list">
                        {portfolio_item}
                    </div>
                </div>
            </div>
        )
    }
}

export default MainPortfolio;