import React, { Component } from 'react';

class Org extends Component{
    render(){
        return(
            <main className="container">
                <article>

                    <header className="subtop subtop--bg1">
                        <div className="inner">
                            <div className="subtop__content">
                                <h2 className="subtop__title">ABOUT US</h2>
                                <p className="subtop__text">
                                    고객의 꿈을 함께 꾸고 만드는 맞춤형 웹서비스 전문 CHSOFT입니다.
                                </p>
                            </div>
                        </div>
                    </header>

                    <div className="contents">
                        <div className="inner">
                            <div className="org">
                                <p className="org__text">
                                    차별화된 서비스를 위한 CHSOFT의 노력입니다.
                                </p>
                                <h3 className="org__title">
                                    ORGNIZATION
                                </h3>
                                <div className="org__content">
                                    <p>
                                        <img src={require("../../img/info/org_1.jpg")} alt="조직도" className="pc"/>
                                    </p>
                                    <p>
                                        <img src={require("../../img/info/org_2.jpg")} alt="조직도" className="mobile"/>
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>

                </article>
            </main>
        )
    }
}

export default Org;