import React, { Component } from 'react';
import QaModal from '../../components/Main/common_qa';

class ContactUs extends Component{
    render(){
        return(
            <main className="container">
                <article>

                    <header className="subtop subtop--bg1">
                        <div className="inner">
                            <div className="subtop__content">
                                <h2 className="subtop__title">ABOUT US</h2>
                                <p className="subtop__text">
                                    고객의 꿈을 함께 꾸고 만드는 맞춤형 웹서비스 전문 CHSOFT입니다.
                                </p>
                            </div>
                        </div>
                    </header>

                    <div className="contents">                        
                        <div className="contactus">
                            <div className="inner">     
                                <h3 className="contactus__title">
                                    CONTACT US
                                </h3>
                            </div>
                            <div className="contactus__content">
                                <div className="contactus__map">
                                    <div className="inner">
                                        <div class="contactus__map__content">

                                        </div>
                                    </div>
                                </div>
                                <div className="contactus__info">
                                    <div className="inner">
                                        <div className="contactus__info__image">
                                            <img src={require("../../img/info/contact_logo.png")} alt="chsoft"/>
                                        </div>
                                        <div className="contactus__info__content">
                                            <ul>
                                                <li className="en">TEL. 070-7012-2881</li>
                                                <li className="en">E-mail. admin@chsoft.co.kr</li>
                                                <li>
                                                    <span className="en">MON-FRI 09:00-18:00</span>
                                                    <em>(주말,공휴일 휴무)</em>
                                                </li>
                                                <li>
                                                    대구광역시 서구 와룡로 307 8층 804호,   서울특별시 성동구 성수일로12길 11 3층
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <QaModal/>
                        </div>
                    </div>

                </article>
            </main>
        )
    }
}

export default ContactUs;