import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Swiper, SwiperSlide } from 'swiper/react';
import MainPortfolio from './main_portfolio';
import QaModal from './common_qa';

import '../../css/main.scss';
import 'swiper/css';
import "swiper/css/pagination";

// import required modules
import { Pagination } from "swiper";

class Main extends Component {
   
    render(){
        const pagination = {
            clickable: true,
        }
        return(
            <main className="container">
                <article>
                    
                    {/* visual{ */}
                    <div className="visual">
                        <Swiper
                            className="visual__slider"
                            spaceBetween={0}
                            slidesPerView={1}
                            onSlideChange={() => console.log('slide change')}
                            onSwiper={(swiper) => console.log(swiper)}
                            pagination={pagination}
                            modules={[Pagination]}
                        >
                            <SwiperSlide className="visual__slide visual__slide--bg1">
                                <div className="inner">
                                    <h2 className="visual__title">
                                        반응형 홈페이지 제작
                                    </h2>
                                    <p className="visual__text">
                                        디바이스의 디스플레이 종류에 반응하여 유기적 배치가 되도록 제작
                                    </p>
                                    <p className="visual__btn-box">
                                        <Link to="/contactus" className="visual__link">
                                            <span>견적문의</span>
                                        </Link>
                                    </p>
                                </div>
                            </SwiperSlide>
                            <SwiperSlide className="visual__slide visual__slide--bg2">
                                <div className="inner">
                                    <h2 className="visual__title">
                                        PC, MOBILE APP SET
                                    </h2>
                                    <p className="visual__text">
                                        최신 트렌드를 반영하여 두가지 고민을 한번에 해결해드립니다.
                                    </p>
                                    <p className="visual__btn-box">
                                        <Link to="/contactus" className="visual__link">
                                            <span>견적문의</span>
                                        </Link>
                                    </p>
                                </div>
                            </SwiperSlide>
                            <SwiperSlide className="visual__slide visual__slide--bg3">
                                <div className="inner">
                                    <h2 className="visual__title">
                                        PC 단독형, MOBILE 단독형
                                    </h2>
                                    <p className="visual__text">
                                        필요없는 상품을 강요하지 않습니다. 필요한 것만 골라 도와드립니다.
                                    </p>
                                    <p className="visual__btn-box">
                                        <Link to="/contactus" className="visual__link">
                                            <span>견적문의</span>
                                        </Link>
                                    </p>
                                </div>
                            </SwiperSlide>
                            <SwiperSlide className="visual__slide visual__slide--bg4">
                                <div className="inner">
                                    <h2 className="visual__title">
                                        맞춤형 제작
                                    </h2>
                                    <p className="visual__text">
                                        싸게, 빨리 찍어내기 식의 홈페이지가 아닌 고객의 의견을 반영한 맞춤형 홈페이지
                                    </p>
                                    <p className="visual__btn-box">
                                        <Link to="/contactus" className="visual__link">
                                            <span>견적문의</span>
                                        </Link>
                                    </p>
                                </div>
                            </SwiperSlide>
                            <SwiperSlide className="visual__slide visual__slide--bg5">
                                <div className="inner">
                                    <h2 className="visual__title">
                                        웹ERP
                                    </h2>
                                    <p className="visual__text">
                                        어디서나 접속 가능한 웹ERP 합리적인 비용에 꼭 맞는 맞춤형 ERP를 제작하세요.
                                    </p>
                                    <p className="visual__btn-box">
                                        <Link to="/contactus" className="visual__link">
                                            <span>견적문의</span>
                                        </Link>
                                    </p>
                                </div>
                            </SwiperSlide>
                            <SwiperSlide className="visual__slide visual__slide--bg6">
                                <div className="inner">
                                    <h2 className="visual__title">
                                        출판ERP
                                    </h2>
                                    <p className="visual__text">
                                        20여년의 경험과 10년 이상의 개발 노하우로 출판 ERP제작
                                    </p>
                                    <p className="visual__btn-box">
                                        <Link to="/contactus" className="visual__link">
                                            <span>견적문의</span>
                                        </Link>
                                    </p>
                                </div>
                            </SwiperSlide>
                        </Swiper>
                    </div>
                    {/* }visual */}

                    {/* section1 */}
                    <div className="section1">
                        <div className="inner">
                            <p className="section1__text">
                                안정적인 유지보수와 빠른 대응을 약속드립니다.<br/>                            
                                정품 프로그램 사용만을 준수하여 정직하게 책임지겠습니다.
                            </p>
                        </div>
                    </div>
                    {/* section1 */}

                    {/* section2 */}
                    <div className="section2">
                        <div className="inner">
                            <h2>PORTFOLIO</h2>
                            <p className="section2__text">
                                관공서, 일반 사기업 등 다양한 분야를 담당하며 쌓은 신뢰성과 경험으로<br/>
                                만족스러운 홈페이지를 제작해드리겠습니다.
                            </p>
                            <p className="section2__btn-box">
                                <Link to="/" className="section2__link">
                                    <span>more+</span>
                                </Link>
                            </p>
                        </div>
                        
                        <MainPortfolio/>
                        
                    </div>
                    {/* section2 */}

                    {/* section3{ */}
                    <QaModal/>
                    {/* }section3 */}

                </article>
            </main>
        )
    }
}

export default Main;