import React, { Component } from 'react';
import { Link } from 'react-router-dom';
class Erp extends Component {
    render(){
        return(
            <main className="container">
                <article>

                    <header className="subtop subtop--bg1">
                        <div className="inner">
                            <div className="subtop__content">
                                <h2 className="subtop__title">TECHNOLOGY</h2>
                                <p className="subtop__text">
                                    고객의 꿈을 함께 꾸고 만드는 맞춤형 웹서비스 전문 CHSOFT입니다.
                                </p>
                            </div>
                        </div>
                    </header>

                    <div className="contents">
                        <div className="inner">
                            <div className="technology">
                                <p className="technology__text">
                                    고객님과 함께 꿈을 만듭니다.
                                </p>
                                <h3 className="technology__title">
                                    ERP
                                </h3>

                                <div className="tab">
                                    <ul className="tab__list">
                                        <li className="tab__item">
                                            <Link to="/homepage" className="tab__link">
                                                <span>홈페이지</span>
                                            </Link>
                                        </li>
                                        <li className="tab__item">
                                            <Link to="/erp" className="tab__link active"> 
                                                <span>ERP</span>
                                            </Link>
                                        </li>
                                        <li className="tab__item">
                                            <Link to="/bookerp" className="tab__link">
                                                <span>출판ERP</span>
                                            </Link>
                                        </li>
                                    </ul>
                                </div>

                                <div className="technology__info">
                                    <div className="technology__info__image">
                                        <p>
                                            <img src={require("../../img/tech/tech_img_02.png")} alt=""/>
                                        </p>
                                    </div>
                                    <div className="technology__info__content">
                                        <p className="technology__info__text1">
                                            설치형이 아닌 인터넷으로 접속가능한 ERP!
                                        </p>
                                        <h4 className="technology__info__title">
                                            언제 어디서나 접속 가능한 웹ERP
                                        </h4>
                                        <p className="technology__info__text2">
                                            생산, 영업, 입고, 출고, 재고, 일정관리까지 ERP와 그룹웨어를 한번에! 값비싼 설치형 ERP보다는 저렴한 비용에 고객님 회사에 꼭 맞는 맞춤형 ERP를 제작하세요.<br/>
                                            CHSOFT는 오랜 시간 경험을 통해 획득한 노하우로 고객님 회사에서 원하는 기능을 제공합니다.
                                        </p>
                                        <div className="technology__info__btn-box">
                                            <Link to="/contactus" class="js-btn">
                                                <span>제작, 견적문의</span>
                                            </Link>
                                        </div>
                                    </div>
                                </div>

                                <div className="technology__portfolio">
                                    <div className="technology__portfolio__item">
                                        <div className="technology__portfolio__image">
                                            <p>
                                                <img src={require("../../img/tech/tech_port_img_04.png")} alt=""/>
                                            </p>
                                        </div>
                                        <div className="technology__portfolio__btn-box">
                                            <a href="http://erp2.testsite.co.kr/open_content/erp_login.php?mode=demo" target="_blank" className="pot_btn cy" rel="noopener noreferrer">
                                                제조 ERP 체험
                                            </a>
                                        </div>
                                    </div>
                                    <div className="technology__portfolio__item">
                                        <div className="technology__portfolio__image">
                                            <p>
                                                <img src={require("../../img/tech/tech_port_img_05.png")} alt=""/>
                                            </p>
                                        </div>
                                        <div className="technology__portfolio__btn-box">
                                            <a href="http://sales.testsite.co.kr/login.php?mode=demo" target="_blank" className="pot_btn cy" rel="noopener noreferrer">
                                                물류 ERP 체험
                                            </a>
                                        </div>
                                    </div>
                                    <div className="technology__portfolio__item">
                                        <div className="technology__portfolio__image">
                                            <p>
                                                <img src={require("../../img/tech/tech_port_img_06.png")} alt=""/>
                                            </p>
                                        </div>
                                        <div className="technology__portfolio__btn-box">
                                            <a href="http://crm.testsite.co.kr/open_content/login/login.php?mode=demo" target="_blank" className="pot_btn cy" rel="noopener noreferrer">
                                                CRM 체험
                                            </a>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>

                </article>
            </main>
        )
    }
}

export default Erp;