import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

function QaModal() {   
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return(
        <>
        <div className="section3">
            <div className="inner">
                <h2>견적 및 제작문의 <span>문의를 남겨주시면 친절하게 답변드리겠습니다.</span></h2>
                <div className="section3__content">
                    <form>
                        <fieldset>
                            <legend className="blind">견적 및 제작문의 폼</legend>
                            <table>
                                <caption className="blind">문의폼</caption>
                                <colgroup>
                                    <col style={{ width:'122px' }}/>
                                    <col/>
                                    <col style={{ width:'122px' }}/>
                                    <col/>
                                    <col style={{ width:'122px' }}/>
                                    <col/>
                                </colgroup>
                                <tbody>
                                    <tr>
                                        <th>
                                            <span className="lable">문의유형</span>
                                        </th>
                                        <td colSpan="5">
                                            <label className="cheackbox">
                                                <input type="checkbox" id="chk1" name="re_cate1" value="1" className="blind"/>
                                                <span className="cheackbox__mark"></span>
                                                홈페이지 제작 문의
                                            </label>
                                            <label className="cheackbox ml_24">
                                                <input type="checkbox" id="chk1" name="re_cate2" value="2"  className="blind"/>
                                                <span className="cheackbox__mark"></span>
                                                ERP 제작 문의
                                            </label>
                                            <label className="cheackbox ml_24">
                                                <input type="checkbox" id="chk1" name="re_cate3" value="3"  className="blind"/>
                                                <span className="cheackbox__mark"></span>
                                                기타 문의
                                            </label>
                                        </td>
                                    </tr>
                                    <tr>
                                        <th>
                                            <label for="re_name" className="lable">이름</label>
                                        </th>
                                        <td>
                                            <input type="text" id="re_name" name="re_name" className="frm-input"/>
                                        </td>
                                        <th>
                                            <label for="re_tel" className="lable">연락처</label>
                                        </th>
                                        <td>
                                            <input type="number" id="re_tel" name="re_tel" className="frm-input"/>
                                        </td>
                                        <th>
                                            <label for="re_email" className="lable">이메일</label>
                                        </th>
                                        <td>
                                            <input type="email" id="re_email" name="re_email" className="frm-input"/>
                                        </td>
                                    </tr>
                                    <tr>
                                        <th colSpan="6">
                                            <label for="memo" className="lable">문의내용</label>
                                        </th>
                                    </tr>
                                    <tr>
                                        <td colSpan="6">
                                            <textarea name="re_content" id="re_content" className="frm-input"></textarea>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                            <div className="section3__btn-box">
                                <label className="cheackbox">
                                    <input type="checkbox" name="agree" id="agree" value="1" className="blind"/>
                                    <span className="cheackbox__mark"></span>
                                    개인정보처리방침에 동의
                                </label>
                                <button type="button" onClick={handleShow}>약관보기</button>                                    
                            </div>
                            <div className="section3__btn-box">
                                <button type="button" className="btn-qa">
                                    <span>문의하기</span>
                                </button>
                            </div>
                        </fieldset>
                    </form>
                </div>
            </div>
        </div>

        <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
                <Modal.Title>개인정보처리방침</Modal.Title>
            </Modal.Header>
                <Modal.Body>
                    <div className="remodal_body">
                        CHSOFT<br/>
                        1. 개인정보의 처리 목적<br/>
                        개인정보를 다음의 목적을 위해 처리합니다. 처리한 개인정보는 다음의 목적 이외의 용도로는 사용되지 않으며 이용 목적이 변경될 시에는 사전 동의를 구할 예정입니다.<br/>
                        당 사이트는 서비스 제공 및 민원처리, 소관업무 수행 등을 목적으로 “이용약관”에 제시하는 내용에 따라 최소한의 개인정보를 수집합니다.<br/>
                        각 정보는 홈페이지를 통해 수집하며 수집목적은 다음과 같습니다.<br/><br/>

                        1. 공공 I-PIN 혹은 휴대폰번호 인증에 따른 본인 인증 정보 : 서비스이용에 따른 본인식별, 통합인증 서비스 제공 목적<br/><br/>

                        2. 사회관계망서비스(SNS : 페이스북, 트위터, 카카오, 네이버 등)<br/><br/>

                        인증을 이용한 정보 : 서비스이용에 따른 본인식별, 통합인증 서비스 제공 목적<br/><br/>

                        3. 이메일, 핸드폰번호, 생년월일 : 고지사항 전달, 본인의사확인, 불만처리 등 원활한 의사소통 경로의 확보, 새로운 서비스 등 최신정보의 안내 목적<br/><br/>

                        2. 개인정보의 처리 및 보유기간<br/>
                        가. 이용자 정보<br/>
                        보유근거 : 개인정보보호법 제15조제1항에 의거 정보주체의 동의를 통한 개인정보 수집 및 이용<br/>
                        보유기간 : 개인정보는 수집·이용 목적으로 명시한 범위 내에서 처리하며, 개인정보보호법 및 관련 법령에서 정하는 보유기간을 준용하여 이행.<br/>

                        나. 서비스 이용이력 (토론방, 국민청원, 관람, 이벤트 응모 등)<br/>
                        보유근거 : 분쟁 조정을 위한 기록보존<br/><br/>

                        3. 개인정보의 제3자 제공에 관한 사항관을 준용합니다.
                    </div>
                </Modal.Body>
            <Modal.Footer>                
                <Button variant="primary" onClick={handleClose}>
                    확인
                </Button>
            </Modal.Footer>
        </Modal>
        </>
    )
    
}

export default QaModal;