import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';


export default function Header(){   

    const [ScrollY, setScrollY] = useState(0);
    const [ScrollActive, setScrollActive] = useState(false);
    const [isActive, setIsActicve] = useState(false);
    const [isOpen, setIsOpen] = useState(false);

    const handleClick= () => {        
        document.querySelector('.submenu-2dept').classList.remove('submenu-2dept--open');       
        setIsActicve(current => !current);        
    }

    const handleClick_2deptopen = () => {       
        setIsOpen(current => !current);
    }
    
    function handleScroll() {
        if (ScrollY > 100) {
            setScrollY(window.pageYOffset);
            setScrollActive(true);
        } else {
            setScrollY(window.pageYOffset);
            setScrollActive(false);
        }
    }
    
    useEffect(() => {
        function scrollListener() {
            window.addEventListener("scroll", handleScroll);
        } //  window 에서 스크롤을 감시 시작
        scrollListener(); // window 에서 스크롤을 감시
        return () => {
            window.removeEventListener("scroll", handleScroll);
        }; //  window 에서 스크롤을 감시를 종료

        
    });

    
    
    return(
        <>
        
        <header className={ScrollActive ? "header fixed" : "header"}>
            <div className="inner">
                <div className="header__inner">
                    <h1 className="logo">
                        <Link to="/" className="logo__link">
                            <span className="blind">Chsoft</span>
                        </Link>
                    </h1>
                    <nav className="gnb">
                        <h2 className="blind">주메뉴</h2>
                        <ul className="gnb__list">
                            <li className="gnb__item">
                                <Link to="/aboutus" className="gnb__link">ABOUT US</Link>
                                <ul className="lnb">
                                    <li className="lnb__item">
                                        <Link to="/aboutus" className="lnb__link">ABOUT US</Link>
                                    </li>
                                    <li className="lnb__item">
                                        <Link to="/history" className="lnb__link">HISTORY</Link>
                                    </li>
                                    <li className="lnb__item">
                                        <Link to="/org" className="lnb__link">조직도</Link>
                                    </li>
                                    <li className="lnb__item">
                                        <Link to="/contactus" className="lnb__link">CONTACT US</Link>
                                    </li>
                                </ul>
                            </li>
                            <li className="gnb__item">
                                <Link to="/homepage" className="gnb__link">TECHNOLOGY</Link>
                            </li>
                            <li className="gnb__item">
                                <Link to="/portfolio" className="gnb__link">PORTFOLIO</Link>
                            </li>
                            <li className="gnb__item">
                                <Link to="/news" className="gnb__link">CH NEWS</Link>
                            </li>                                
                        </ul>
                    </nav>
                    <button type="button" className="btn-menu" onClick={handleClick}>
                        <span className="blind">토글메뉴</span>
                    </button>
                </div>
            </div>
        </header>

        <div className={ isActive ? 'mobile-gnb mobile-gnb--open' : 'mobile-gnb'} >
            <div className="mobile-gnb__head">
                <h3 className="mobile-gnb__head__logo">
                    <Link to="/" className="mobile-gnb__head__logo__link">
                        <span className="blind">chsoft</span>
                    </Link> 
                </h3>
                <span className="btn-close" onClick={handleClick}></span>
            </div>
            <div className="mobile-gnb__body">
                <nav className="gnb">
                    <h3 className="blind">주메뉴</h3>
                    <ul className="gnb__list">
                        <li className="gnb__item">
                            <span className="gnb__link" onClick={handleClick_2deptopen}>ABOUT US</span>
                            <ul className={ isOpen ? 'submenu-2dept submenu-2dept--open' : 'submenu-2dept'}>
                                <li className="submenu-2deptb__item">
                                    <Link to="/aboutus" className="submenu-2dept__link" onClick={handleClick}>ABOUT US</Link>
                                </li>
                                <li className="submenu-2dept__item">
                                    <Link to="/history" className="submenu-2dept__link" onClick={handleClick}>HISTORY</Link>
                                </li>
                                <li className="submenu-2dept__item">
                                    <Link to="/org" className="submenu-2dept__link" onClick={handleClick}>조직도</Link>
                                </li>
                                <li className="submenu-2dept__item">
                                    <Link to="/contactus" className="submenu-2dept__link" onClick={handleClick}>CONTACT US</Link>
                                </li>
                            </ul>
                        </li>
                        <li className="gnb__item">
                            <Link to="/homepage" className="gnb__link gnb__link--no-bg" onClick={handleClick}>TECHNOLOGY</Link>
                        </li>
                        <li className="gnb__item">
                            <Link to="/portfolio" className="gnb__link gnb__link--no-bg" onClick={handleClick}>PORTFOLIO</Link>
                        </li>
                        <li className="gnb__item">
                            <Link to="/news" className="gnb__link gnb__link--no-bg" onClick={handleClick}>CH NEWS</Link>
                        </li>  
                    </ul>
                </nav>
            </div>
        </div>
        </>
    )
}

